
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:"message",
components:{
ButtonTab,
ButtonTabItem,
Group,
Cell
},
data() {
return {
selector:0,
hasReadMsgs:null,
hasNotReadMsgs:null
}
},
created() {

var that = this;
this.$http.get('/messages',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
if(res.data){
that.hasReadMsgs=res.data.has_read_messages;
that.hasNotReadMsgs=res.data.hasnot_read_messages;
}
})
},
}



