
import Msg from 'vux/src/components/msg/index.vue'

import {mapMutations} from 'vuex'
export default {
data() {
return {
isShowMsg:false,
buttons:[
{
type:'primary',
text:'跳转到登录页面',
link:'/login'
}
]
}
},
name:"logout",
components:{
Msg
},
created() {
if(false==this.$store.state.isLogin){
this.isShowMsg=true
return
}
this.mutationsLogOut()
window.localStorage.clear();
this.$router.push('/topiclist/all');
},
methods: {
...mapMutations(['mutationsLogOut'])
},
}



