
import {mapState} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name:"topiclist",
props:['name'],
components:{
Group,
Cell,
Scroller,
Tab,
TabItem
},
computed:{
...mapState(['topics'])
},
data:function(){
return{
list:[],

page:1,

limit:15,
pullupConfig:{
content:"上拉加载更多",
downContent:'松开进行加载',
upContent:"上拉加载更多",
loadingContent:"加载中"
},
pulldownConfig:{
content:"下拉刷新",
downContent:'松开进行加载',
upContent:"下拉加载更多",
loadingContent:"加载中"
}
}
},
watch:{
$route:function(to,from){
this.fectData()
}
},
created() {
this.fectData()
},
methods: {
freshData:function(){
var that=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){
if(res.data){
that.list=res.data.data
that.page=1
that.$refs.pullUp.donePulldown()
}
})
},

routeto(index){
this.$router.push('/topiclist/'+this.topics[index].name).catch(error => error)
},
loadMore:function(){
this.page=this.page+1

var that = this
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
if(res.data.success){
for(var i=0;i<res.data.data.length;i++){
that.list.push(res.data.data[i])
}


that.$refs.pullUp.donePullup()
}
});
},
fectData:function(){
let that=this
this.$http.get('/topics/',{params:{
tab:this.name,
page:this.page,
limit:this.limit
}}).then(function(res){
if(res.data.success){
that.list=res.data.data;
}



})
},
getDescByTab(tab){
return this.$store.getters.getTopicDescByTab(tab);
}
}
}



