
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import WechatEmotion from 'vux/src/components/wechat-emotion/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapState} from 'vuex';
export default {
name:'topic',
props:['id'],
data:function(){
return{
topic:null,
content:''
}
},
computed:{
...mapState(['isLogin'])
},
components:{
CellBox,
Group,
Flexbox,
FlexboxItem,
WechatEmotion,
XTextarea,
XButton
},
watch:{
$route:function(to,from){
this.fectData(to.params.id)
}
},
created() {
this.fectData()
},
methods: {
fectData:function(next){
let that=this
this.$http.get('/topic/' + this.$route.params.id,{
params:{
accesstoken:that.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
that.topic=res.data.data;
}
if(next){
next()
}
})
},

getDescByTab(tab){
return this.$store.getters.getTopicDescByTab(tab);
},

addreply:function(replyTab){

var that =this;
this.$http.post('/topic/'+this.topic.id+'/replies',{
accesstoken:this.$store.state.accesstoken,
content:this.content
}).then(function(res){

if(res.data&&res.data.success){






}
})
},

collect:function(){
var that =this;
this.$http.post('/topic_collect/collect',{
accesstoken:that.$store.state.accesstoken,
topic_id:that.topic.id
}).then(function(res){
if(res.data&&res.data.success){
that.topic.is_collect=true
}
})
},

noCollect:function(){
var that =this;
this.$http.post('/topic_collect/de_collect',{
accesstoken:that.$store.state.accesstoken,
topic_id:that.topic.id
}).then(function(res){
if(res.data&&res.data.success){
that.topic.is_collect=false
}
})
}
},
}



